import React, { useState, useEffect } from "react"
import axios from "axios";
import '../App.css';
import {FlatList} from "react-native"
import {TaskTile} from "./TaskTile"
import { TaskForm } from "./TaskForm";
export const Actioner= () => {
    
    
    const api = 'https://students.elwinar.com/Faousi/'
    const[tasks, setTasks] = useState([
    ])

    useEffect(() => {
        axios.get(api).then((response)=> {
          setTasks(response.data)
        });
      },[]); 
      if(!tasks) return null;

    const renderItem =({item}) => {
        return <TaskTile task={item} onUpdateTask={onUpdateTask} onDeleteTask={onDeleteTask}/>
    }

    //Fonction creer une tache au state et passer cette fonction a notre form
    const onAddTask = (title) => {
        setTasks([...tasks,{
            id: tasks.length,
            title,
            isCompleted : true
        }])
    }

    const onDeleteTask = (id) => {
        let newTasks =[]
        tasks.forEach(t => {
            if(t.id !== id) {
                newTasks.push(t)
                return
            }
        })

        setTasks(newTasks)
    }

    const onUpdateTask = (id) => {
        let newTasks =[]
        tasks.forEach(t => {
            if(t.id !== id) {
                newTasks.push(t)
                return
            }
            newTasks.push({
                id,
                title: t.title,
                isCompleted : !t.isCompleted
            })
        })

        setTasks(newTasks)
    }

return(

<div class="action">
          
          <div class="taches">
            
            
            <FlatList class="li-container" 
            data={tasks}
            keyExtractor={(item, index) => index.toString()}
            renderItem={renderItem} 
            contentContainerStyle={{flexGrow:1, margin:30}}/>
               
          
          

        </div>


        <div class="task-creator">
        
        <TaskForm onAddTask={onAddTask}/>
        </div>
    

</div>)
}
