
import React, { useState, useEffect } from "react"
import home from "../components/assets/home.svg";
import parametre from "../components/assets/settings.svg";
import off from "../components/assets/logout.svg"
import axios from "axios";
import { Link } from "react-router-dom";
import plus from "../components/assets/plus.png"
import '../list.css';
import Counter from "../components_task/Counter"
import Actioner from "../components_task/Actioner"
export const Listtask = () => {
    var tasks = require ('../components_task/Actioner')
    const api = 'https://students.elwinar.com/Faousi/'
    const [lists, setLists] = useState([])
    
    
      useEffect(() => {
        axios.get(api).then((response)=> {
          setLists(response.data)
        });
      },[]); 
      if(!lists) return null;
  
    
    const handleSubmit = (e) => {
      e.preventDefault()
  
      const name = e.currentTarget.elements.name.value.trim()
  
      if (name.length === 0) {
        
        return
      }

      setLists((prevLists) => [...prevLists, { id: prevLists.length + 1, name }])
      
      e.currentTarget.reset()
      }

      const selectList = (e) =>  {

      }

return(<div class="menu">
          
    <div class="user">
        <Link to="/app" class="home">    <img src={home} class="home" alt="maison"/></Link>

        <p class="adresse">hello</p>

    </div>


    <div class="li-container">
        
        <div class="titre">
          <p>Mes Listes </p> 
          <hr></hr>
        
        </div>
        
       <div class="listes">
        <ul>
        {lists.map((list) => (
        <li key={list.id}>{list.name}<div class="numero"><p><Counter nb={tasks.length}/></p></div> </li>
        ))}
        
        </ul>
        </div>
        <div class="nouvelle">
          <div class="search">
              <form onSubmit={handleSubmit}>
              <button type="submit" class="btn-add">+</button>
              <input type="text" id="name" name="name" placeholder="Nouvelle liste"required />
              </form>
          </div>
        </div>
    </div>
 
     
 
  


    <div class="parametres">
    
        <div className="options">
            <Link to="/parametres" class="lparametre"> <img src={parametre} class="pic1" alt="maison"/></Link>
            <Link to="/parametres" class="p"> <p class="p">Parametres</p></Link> 
        </div>
        
        <div className="out">
            <Link to="/" class="lout"> <img src={off} class="pic2"    alt="maison"/></Link>
            <Link to="/" class="l"> <p class="l">Dconexion</p></Link>
       
        </div>
    </div>

</div>)
}
