import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react"
import axios from "axios";
import {Form} from "react-bootstrap";
import '../parametres.css';
export const Settings = () => {


return (

    
    <div class="settings">
    
    <h2 className="parametres">Paramtres</h2>
    <h2 className="adresse">Adresse e-mail</h2>
    <h2 className="actual">Adresse e-mail actuelle : {}</h2>
    
    <form className="user">
            
            <Form.Group Id="formBasicEmail">
                
                <Form.Label class="email">Nouvelle adresse e-mail</Form.Label>
                <Form.Control type="email" placeholder="mail@provider.com" />
                <Form.Label class="email">Confirmer l'adresse e-mail</Form.Label>
                <Form.Control type="email" placeholder="mail@provider.com" />
                <button type="submit">
                Modifier l'adresse e-mail
                </button>
                
            </Form.Group>
           

    </form>


<form className="motpasse">
<h2 className="tittle">Mot de passe</h2>
             <Form.Group Id="formBasicPassword">
                <Form.Label class="password">Mot de passe actuel</Form.Label>
                <Form.Control type="password" placeholder="password" />
                <Form.Label class="password">Nouveau mot de passe</Form.Label>
                <Form.Control type="password" placeholder="password" />
                <Form.Label class="password">Confirmer le nouveau mot de passe</Form.Label>
                <Form.Control type="password" placeholder="password" />
                <button type="submit">
                Modiefier le mot de passe
                </button>
            </Form.Group>
</form>
</div>
          
  

)
}

